import Header from "../App/Header"
import { Link } from "react-router-dom"
import * as ROUTES from "../../constants/routes"

//MUI
import Typography from "@mui/material/Typography"
import Card from "@mui/material/Card"
import CardContent from "@mui/material/CardContent"
import Stack from "@mui/material/Stack"

export default function Landing() {
  return (
    <>
      <Header title="Welcome" />
      <Stack spacing={2}>
        <Card>
          <CardContent>
            <Typography variant="h5" component="h3">
              Spielbeschreibung
            </Typography>

            <Typography variant="body1">
              Dir wird eine Frage prsentiert zu welcher es vier mgliche Antworten gibt. Wenn du eine Antwort auswhlst, siehst
              du, ob du korrekt geantwortet hast. Wurde die korrekte Antwort gewhlt, so erhlst du die nchste Frage. Wenn du
              aber eine falsche Antwort auswhlt, so ist das Spiel beendet und du hast verloren. Das Ziel ist es, alle Fragen
              korrekt zu beantworten. Es werden dir maximal 15 Fragen gestellt, welche du alle korrekt beantworten musst, um zu
              gewinnen. Mit jeder korrekt beantworteten Frage erhlst du einen Gewinn in Punkteform. Sollte jedoch eine Frage
              falsch beantwortet werden, so fllst du auf die letzte Sicherheitsstufe zurck. Bei der Sicherheitsstufe handelt es
              sich um eine Absicherung fr dich. Wenn eine Sicherheitsstufe berschritten wurde, so wirst du selbst beim Verlieren
              des Spiels den gesicherten Punktestand erhalten. Fr die Beantwortung einer Frage bleiben dir maximal 40 Sekunden.
              Dir stehen whrend dem Spiel zwei Arten von Joker zur Verfgung. Wie viele der Joker du einsetzen kannst, hngt
              davon ab, wie viele Joker du dir bereits erspielt hast.
            </Typography>
          </CardContent>
        </Card>
        <Card>
          <CardContent>
            <Typography variant="h5" component="h3">
              Themengebiete
            </Typography>

            <Typography variant="body1">
              Die zur Verfgung stehenden Themengebiete zum Spielen werden von deiner Lehrperson erstellt. Du kannst ein
              Themengebiet so oft wie du willst wiederholen. Hast du es aber einmal komplett erfolgreich beendet, kannst du es
              nicht mehr wiederholen.
            </Typography>
          </CardContent>
        </Card>

        <Card>
          <CardContent>
            <Typography variant="h5" component="h3">
              Joker
            </Typography>
            <Typography variant="subtitle2" component="div">
              50 : 50
            </Typography>
            <Typography variant="body1">
              Es werden dir zwei zufllige falsche Antworten entfernt und du kannst noch aus zwei Antworten auswhlen.
            </Typography>
            <Typography variant="subtitle2" component="div">
              Timer Stopp
            </Typography>
            <Typography variant="body1">Die Zeit wird angehalten und du kannst die Frage in Ruhe beantworten.</Typography>
            <Typography variant="body1" sx={{ mt: 1 }}>
              Aber Achtung: Der Einsatz eines Jokers fhrt zu einem Punktabzug von 1/4 deiner Gewinnsumme.
            </Typography>
          </CardContent>
        </Card>

        <Card>
          <CardContent>
            <Typography variant="h5" component="h3">
              Level
            </Typography>

            <Typography variant="body1">
              Durch das erspielen von Punkten kannst du ein hheres Level erreichen. Fr jedes Level werden 1 Million Punkte
              bentigt. Das Erreichen eines neuen Level lohnt sich fr dich, denn so erhlst du einen neuen "50:50" und "Timer
              Stopp" Joker. Dein aktuelles Level und dein Punktestand kannst du in den den{" "}
              <Link to={ROUTES.ACHIEVEMENTS}>Achievements</Link> einsehen.
            </Typography>
          </CardContent>
        </Card>

        <Card>
          <CardContent>
            <Typography variant="h5" component="h3">
              Abzeichen
            </Typography>

            <Typography variant="body1">
              Fr besonders gute Leistungen kannst du verschiedene Abzeichen erhalten. Welche Abzeichen du bereits erspielt hast
              und welche noch nicht, kannst du in den <Link to={ROUTES.ACHIEVEMENTS}>Achievements</Link> einsehen.
            </Typography>
          </CardContent>
        </Card>
      </Stack>
    </>
  )
}
